<template>
    <div class="add-to-cart">
        <button class="btn btn-primary" @click="addToCart">Add to Cart</button>
    </div>
    
</template>


<script>
export default {
    name: "add-to-cart",
    props: {
        msg: String,
        name: String,
        price: String,
        pId: String,
        productImage: String,
    },
    data(){
        return {

            item:{
                    productName: this.name,
                    productPrice: this.price,
                    productId: this.pId,
                    productImage: this.productImage,
                    productQuantity: 1
            }
 
        }
    },
    methods: {

        addToCart(){
            $("#miniCart").modal("show")
            this.$store.commit("addToCart", this.item)
            // $("#miniCart").modal("hide")
        }
    },
    created(){

    }
}
</script>

