<template>
    <div class="products">
        <div class="container">
            <div class="intro h-100">
                    <div class="row h-100 justify-content-center align-items-center">
                    <div class="col-md-6">
                        <h3>Order Page</h3>
                        
                        <p>
                        Lorem ipsum dolor, sit amet consectetur adipisicing elit. Unde, ducimus.
                        </p>
                    </div>
                    <div class="col-md-6">
                        <img src="/img/svg/products.svg" alt="" class="img-fluid">
                    </div>
                    </div>
             </div>

          

             <hr>
             <h3 class="d-inline-block">Products List</h3>
             <button @click="addNew" class="btn btn-primary float-right" >Add Product</button>

            <div class="table-responsive">
             <table class="table">
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>Price</th>
                            <th>Modify</th>

                        </tr>
                    </thead>

                    <tbody>
                        <tr v-for="product in products">
                            <td>
                                {{product.name}}
                            </td>
                            <td>
                                {{product.price}}
                            </td>
                            <td>
                                <button class="btn btn-warning" @click="editProduct(product)">Edit</button>
                                <button class="btn btn-danger" @click="deleteProduct(product)">Delete</button>

                            </td>
                        </tr>
                        
                    </tbody>
     
                </table>
            </div>
                
    </div>

    
      <!-- Modal -->
      <div class="modal fade" id="product" tabindex="-1" role="dialog" aria-labelledby="editLabel" aria-hidden="true">
        <div class="modal-dialog modal-lg" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="editLabel" v-if="this.modal == 'new'">Add Product</h5>
              <h5 class="modal-title" id="editLabel" v-if="this.modal == 'edit'">Edit Product</h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">

                <div class="row">
                  <!-- main product -->
                  <div class="col-md-8">
                    <div class="form-group">
                      <input type="text" v-model="product.name" placeholder="Product Name" class="form-control">
                    </div>

                    <div class="form-group">
                      <vue-editor v-model="product.desc" ></vue-editor>
                    </div>
                  </div>
                  <!-- product sidebar -->
                  <div class="col-md-4">
                    <h4 class="display-6">Product Details</h4>
                    <hr>

                    <div class="form-group">
                      <input type="text" v-model="product.price" placeholder="Product price"  class="form-control">
                    </div>

                    <div class="form-group">
                      <input type="text" v-model="tag" placeholder="Product tags"  @keyup.188="addTag" class="form-control">
                      
                      <div  class="d-flex">
                              <p v-for="tag in product.tags">
                                <span class="p-1" @click="deleteTag(tag, index)" >{{tag}}</span>
                              </p>               
                      </div>
                    </div>


                    <div class="form-group">
                      <label for="product_image">Product Images</label>
                      <input type="file"  class="form-control" @change="uploadImage">
                    </div>

                    <div class="form-group d-flex">
                      <div  v-for="image in product.images">
                          <div class="img-wrapp p-1">
                              <img :src="image" alt="" width="50px" height="50px">
                              <span class="delete-img" @click="deleteImage(image, index)">X</span>
                          </div>
                      </div>
                    </div>

                  </div>
                </div>




            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
              <button @click="addProduct()"  type="button" class="btn btn-primary" v-if="this.modal == 'new'" >Save changes</button>
              <button @click="updateProduct()" type="button" class="btn btn-warning" v-if="this.modal == 'edit'" >Apply changes</button>
            </div>
          </div>
        </div>
      </div>





    </div>
</template>

<script>

import { fb, db } from "../firebase";
import { VueEditor } from "vue2-editor";        

export default {
    name: "Products",
    props: {
        msg: String
    },
    components: {
        VueEditor
    },
    data(){
      return {
          products: [],
          product: {
               name: null,
               price: null,
               desc: null,
               tags: [],
               images: []
          },
          activeItem: null,
          modal: null,
          tag: null
         
        }

    },
    // WE USE VUE-FIRESTORE FOR THE REALTIME DATABASE UPDATE
    firestore(){
        return {
            products: db.collection("products")
        }
    },

    methods: {
        addTag(){
            this.product.tags.push(this.tag);
            this.tag = "";
            // console.log("Presssing comma")
        },

        deleteTag(tag, index){
            this.product.tags.splice(index, 1);

        },

        reset(){
            this.product = {
                    name: null,
                    price: null,
                    desc: null,
                    tags: [],
                    images: []
            }
        },

        addNew(){
            this.modal = "new"
            this.reset();
            $("#product").modal("show");
        },

        deleteImage(img, index){

            let image = fb.storage().refFromURL(img);
            //remove product image from array list called product
            this.product.images.splice(index, 1);

            image.delete().then(()=>{
                console.log("Image deleted");
            }).catch( err => console.log("an error occured", err) );


        },

        uploadImage(e){

            if(e.target.files[0]){  
                 let file = e.target.files[0];
                // Create a root reference
                var storageRef = fb.storage().ref("products/"+ Math.random() + '_' + file.name);
                let uploadTask = storageRef.put(file);

                    // 3. Completion observer, called on successful completion
                    uploadTask.on('state_changed', (snapshot)=>{
        
                    }, (error)=> {
                    // Handle unsuccessful uploads
                        console.log(error)
                    }, ()=> {
                    // Handle successful uploads on complete
                    // For instance, get the download URL: https://firebasestorage.googleapis.com/...
                    uploadTask.snapshot.ref.getDownloadURL().then((downloadURL)=> {
                        console.log('File available at', downloadURL);
                        this.product.images.push(downloadURL)
                    });
                    });
                // console.log(e.target.files[0])

            }
                   
            },
      

        readData(){
           
        },

        addProduct(){
            //For realtime data using firestore
            this.$firestore.products.add(this.product);
              

                $('#product').modal('hide');

                   Toast.fire({
                    type: 'success',
                    title: 'Updated  successfully'
                });

            },


        deleteProduct(doc){
            Swal.fire({
                title: 'Are you sure?',
                text: "You won't be able to revert this!",
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Yes, delete it!'
            }).then((result) => {
                if (result.value) {

                this.$firestore.products.doc(doc.id).delete();
                // Swal.fire(
                //     "Deleted!",
                //     "Your Documents has been deleted.",
                //     "success"
                // )

                Toast.fire({
                    type: 'success',
                    title: 'Deleted  successfully'
                })

                
                }
            });


        },

        editProduct(product){
            this.product = product;
            this.modal   = "edit";
            $("#product").modal("show");
        },
      updateProduct(){
            this.$firestore.products.doc(this.product.id).update(this.product);
                 Toast.fire({
                    type: 'success',
                    title: 'Updated  successfully'
                });
            $("#product").modal("hide");

        },
        

    },

    created(){
    

    }

    
    
}
</script>

<style scoped lang="scss">
    .img-wrapp{
        position: relative;
    }
    .img-wrapp span.delete-img{
        position: absolute;
        top: -14px;
        left: -2px;
    }
    .img-wrapp span.delete-img:hover{
        cursor: pointer;
    }


</style>